<template>
  <div id="app">
    <div>
      <b-modal id="newsModal" centered title="最新公告" hide-footer>
        <div class="row">
          <div class="col px-2"><img src="./assets/info.png" alt="最新信息" style="width:100%"></div>
        </div>
      </b-modal>
    </div>
    <div class="row no-gutters">
      <!-- left -->
      <div class="col-sm-4 col-lg-3">
        <div class="toolbox">
          <div class="sticky-top bg-white shadow-sm pt-3 pb-2 px-2">
            <div class="form-group d-flex">
              <label for="cityName" class="mr-2 col-form-label text-right">縣市</label>
              <div class="flex-fill">
                <select id="cityName" v-model="select.option" class="form-control" @change="selectArea">
                  <option v-for="c in citiName" :value="c.CityName" :key="c.CityName">{{ c.CityName }}</option>
                </select>
              </div>
            </div>
            <div class="form-group d-flex">
              <label for="area" class="mr-2 col-form-label text-right">地區</label>
              <div class="flex-fill">
              <select id="area" class="form-control" v-model="select.town" @change="townSelect">
                <option value="">-- 查詢區域 --</option>
                <option v-for="a in select.areaList" :value="a.AreaName" :key="a.AreaName">{{ a.AreaName }}</option>
              </select>
              </div>
            </div>
            <p class="mb-0 small text-muted text-right" v-if="select.town == ''">請選擇地區查詢</p>
            <div v-show="select.town != ''">
              <p class="mb-0 small text-muted text-right" v-if="select.properties != ''">此地區有 {{ select.properties.length }} 間藥局</p>
              <p class="mb-0 small text-muted text-right" v-else="">此地區有沒有配合發放的藥局</p>
            </div>
          </div>
          <ul class="list-group" v-show="select.properties != ''">
            <template  v-for="(p, index) in select.properties" >
              <a class="list-group-item text-left" :key="p.properties.name" @click="showPopup(index)">
                <div class="d-flex align-items-center">
                  <h3 class="mb-0">{{ p.properties.name }}</h3>
                </div>
                  <div class="d-flex  justify-content-start mt-2" style="height:2.5rem">
                    <div class="d-flex w-50 align-item-center justify-content-center px-1  rounded-lg text-white" >
                      <p class="my-auto mr-1">成人</p>
                      <p class="font-weight-bold my-auto adultNum" style="font-size:1.2rem">{{ p.properties.mask_adult }}</p>
                    </div>
                    <div class="ml-2 d-flex w-50 align-item-center justify-content-center px-1 rounded-lg text-white" >
                      <p class="my-auto mr-1">孩童</p>
                      <p class="font-weight-bold my-auto childNum" style="font-size:1.2rem" >{{ p.properties.mask_child }}</p>
                    </div>
                  </div>
                <p class="mb-0">{{ p.properties.phone }}</p>
                <p class="mb-0"><a :href="'https://www.google.com/maps/dir/?api=1&destination='+p.properties.name"
                  target="_blank" title="Google Map" class="text-decoration-none">
                  <a :href="'https://www.google.com/maps/dir/?api=1&destination='+p.properties.name"
                      target="_blank" title="Google Map" class="">
                      <b-icon icon="cursor" class=""></b-icon>
                    </a>
                    {{ p.properties.address }}</a>
                </p>
              </a>
            </template>
          </ul>
          </div>
        </div>
      <!-- right -->
      <div class="col-sm-8 col-lg-9">
        <div id="map"></div>
      </div>
    </div>
  </div>
</template>

<script>
import citiName from './assets/cityCountyData.json'
import L from 'leaflet'

var osmMap = {}

export default {
  name: 'App',
  data: () => ({
    data: [],
    citiName,
    select: {
      option: '臺北市',
      areaList: [],
      town: '',
      properties: ''
    },
    nowM: 0,
    nowC: 0
  }),
  mounted () {
    this.$bvModal.show('newsModal')
    const url = 'https://raw.githubusercontent.com/kiang/pharmacies/master/json/points.json'
    this.$http.get(url).then((res) => {
      this.data = res.data.features
      const citiArea = this.citiName.filter(val => val.CityName === this.select.option)
      this.select.areaList = citiArea[0].AreaList
    }).catch((res) => console.log(res))
    osmMap = L.map('map', {
      center: [25.03, 121.55],
      zoom: 15
    })
    L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png?{foo}', {
      foo: 'bar', attribution: 'Map data &copy; <a href="https://www.openstreetmap.org/">OpenStreetMap</a> contributors, <a href="https://creativecommons.org/licenses/by-sa/2.0/">CC-BY-SA</a>', maxZoom: 18
    }).addTo(osmMap)
  },
  watch: {
    'select.properties' () {
      this.$nextTick(() => {
        this.spaceClr()
      })
    }
  },
  methods: {
    selectArea () {
      this.removeMarker()
      this.select.properties = ''
      this.select.town = ''
      this.select.areaList = []
      const citiArea = this.citiName.filter(val => val.CityName === this.select.option)
      this.select.areaList = citiArea[0].AreaList
    },
    townSelect () {
      this.removeMarker()
      document.querySelector('.toolbox').scrollTop = 0
      const propertiesList = this.data.filter((property) => property.properties.town === this.select.town && property.properties.county === this.select.option)
      this.select.properties = propertiesList
      if (this.select.properties.length > 0) {
        propertiesList.forEach((listArea) => {
          const { properties, geometry } = listArea
          const clr = properties.mask_adult > 0 ? 'bg-success' : 'bg-danger'
          const chilClr = properties.mask_child > 0 ? 'bg-info' : 'bg-danger'
          L.marker([
            geometry.coordinates[1],
            geometry.coordinates[0]
          ]).addTo(osmMap).bindPopup(`
              <div class=" text-center font-weight-bold" style="font-size:1.2rem" >${properties.name}</div>
              <div class="d-flex justify-content-center mt-2" style="height:2.5rem">
                <div class="d-flex align-item-center ${clr} justify-content-center px-1 w-50  rounded-lg text-white" >
                  <p class="my-auto mr-1">成人</p>
                  <p class="font-weight-bold my-auto adultNum" style="font-size:1.2rem">${properties.mask_adult}</p>
                </div>
                <div class="ml-2 d-flex align-item-center ${chilClr} justify-content-center px-1 w-50 rounded-lg text-white" >
                  <p class="my-auto mr-1">孩童</p>
                  <p class="font-weight-bold my-auto childNum" style="font-size:1.2rem">${properties.mask_child}</p>
                </div>
              </div>
              <div class="mt-2" style="font-size:.8rem">
                <div>${properties.phone}</div>
                <div><a href="https://www.google.com/maps/dir/?api=1&destination=${properties.name}" target="_blank">${properties.address}</a></div>
              </div>
              <div class="text-center border-top mt-2 pt-2" style="font-size:.5rem">更新時間：${properties.updated}</div>
            `)
        })
        this.panTo(propertiesList[0])
      }
    },
    removeMarker () {
      osmMap.eachLayer(function (layer) {
        if (layer instanceof L.Marker) {
          osmMap.removeLayer(layer)
        }
      })
    },
    panTo (propertiesListFrist) {
      const { geometry } = propertiesListFrist
      osmMap.panTo([
        geometry.coordinates[1],
        geometry.coordinates[0]
      ])
    },
    showPopup (item) {
      const listArea = this.select.properties[item]
      const { properties, geometry } = listArea
      const clr = properties.mask_adult > 0 ? 'bg-success' : 'bg-danger'
      const chilClr = properties.mask_child > 0 ? 'bg-info' : 'bg-danger'
      L.marker([
        geometry.coordinates[1],
        geometry.coordinates[0]
      ]).addTo(osmMap).bindPopup(`
        <div class=" text-center font-weight-bold" style="font-size:1.2rem" >${properties.name}</div>
        <div class="d-flex justify-content-center mt-2" style="height:2.5rem">
          <div class="d-flex align-item-center px-1 w-50 justify-content-center ${clr} rounded-lg text-white">
            <p class="my-auto">成人&nbsp;</p>
            <p class="font-weight-bold my-auto" style="font-size:1.2rem">${properties.mask_adult}</p>
          </div>
          <div class="ml-2 d-flex align-item-center px-1 w-50 justify-content-center ${chilClr} rounded-lg text-white">
            <p class="my-auto">孩童&nbsp;</p>
            <p class="font-weight-bold my-auto" style="font-size:1.2rem">${properties.mask_child}</p>
          </div>
        </div>
        <div class="mt-2" style="font-size:.8rem">
          <div>${properties.phone}</div>
          <div><a href="https://www.google.com/maps/dir/?api=1&destination=${properties.name}" target="_blank">${properties.address}</a></div>
        </div>
        <div class="text-center border-top mt-2 pt-2" style="font-size:.5rem">更新時間：${properties.updated}</div>
      `).openPopup()
    },
    spaceClr () {
      const clrAdult = document.querySelectorAll('p.adultNum')
      clrAdult.forEach((tag) => {
        if (tag.textContent > 1) {
          tag.parentNode.style.backgroundColor = '#28a745'
        } else {
          tag.parentNode.style.backgroundColor = '#dc3545'
        }
      })
      const clrChild = document.querySelectorAll('p.childNum')
      clrChild.forEach((tag) => {
        if (tag.textContent > 1) {
          tag.parentNode.style.backgroundColor = '#17a2b8'
        } else {
          tag.parentNode.style.backgroundColor = '#dc3545'
        }
      })
    }
  }
}
</script>

<style>
  #map {
    height: 100vh;
  }
  .highlight {
    background: #e9ffe3;
  }
  .toolbox {
    height: 100vh;
    overflow-y: auto;
  }
  .toolbox  a {
    cursor: pointer;
  }
</style>
